.description {
  background: url('../img/description.jpg') no-repeat;
  padding-bottom: 100px;
  background-size: cover;
  position: relative;
  #description {
    position: absolute;
    left: 0;
    z-index: 80; }
  #description2 {
    position: absolute;
    right: 0;
    z-index: 80; }
  #description3 {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 80; }
  #description4 {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 80; }
  &__text {
    p {
      font-size: 22px;
      line-height: 1.36;
      letter-spacing: 1.1px;
      color: $light-khaki;
      padding-bottom: 30px; } } }
