  content: '';
  position: absolute;
  left: 0;
  bottom: 4px;
  width: 15px;
  height: 2px;
  background: #000000;
  transition: color 0.3s;
  transition: transform 0.5s;
  border-radius: 20%;
}

.menu-open.active span {
  width: 0;
}

